import React, { useState } from 'react';
import axios from 'axios';
import groupBy from 'lodash/groupBy';
import SuggestionItem from './SuggestionItem';
import NoMatch from './NoMatch';

const API_URL = localStorage.getItem('API_URL') || 'http://localhost:3060';
const DEFAULT_ENV = localStorage.getItem('API_ENV') || 'development';

localStorage.setItem('API_URL', API_URL);

export default () => {
  const [searchTerm, setSearchTermState] = useState('');
  const [suggestions, setSuggestions] = useState({});
  const [API, setAPI] = useState(API_URL);
  const [API_ENV, setAPI_ENV] = useState(DEFAULT_ENV);
  const [showAPIModal, toggleAPIModal] = useState(false);
  const [error, setError] = useState('No Match Found !!');

  const apiEnvChanged = ({ target }) => {
    setAPI_ENV(target.value);
    localStorage.setItem('API_ENV', target.value);
    target.value === 'development' ?
      setApiUrl('http://localhost:3060') : target.value === 'staging' ?
      setApiUrl('http://localhost:3060') : setApiUrl('http://localhost:3060');
    setTimeout(() => {
      toggleAPIModal(!showAPIModal);
    }, 500);
  };

  const setApiUrl = url => {
    setAPI(url);
    localStorage.setItem('API_URL', url);
  }

  const searchTermChanged = async ({ target: { value: term }}) => {
    setSearchTermState(term);
    if (term) {
      try {
        const result = await axios.get(`${API}/search/searchAll?term=${term}`).then(response => response.data);
        if (result && result.data && result.data.length) {
          const data = groupBy(result.data, d => d.schemaType);
          setSuggestions(data);
        } else {
          setError('No Match Found !!');
          setSuggestions({});
        }
      } catch (error) {
        setError(error.message);
      }
    } else {
      setSuggestions({});
    }
  }

	return (
    false && <div>
      <i className="fas fa-bars float-right" onClick={() => toggleAPIModal(!showAPIModal)}></i>
			<div className="container" id="search-container">
				<div className="row">
					<input type="text" placeholder="Type anything..." className="form-control col-sm-12" name="username" id="username" value={searchTerm} onChange={searchTermChanged}/>
				</div>
			</div>
      {
        Object.keys(suggestions).length ? Object.keys(suggestions).map(type => {
          return (
            <div className="container" id="suggestion-container" key={type}>
              <SuggestionItem  data={suggestions[type]} type={type} />
            </div>
          )
        }): searchTerm && searchTerm.length > 1 ? <NoMatch error={error}/> : ''
      }
      {
        showAPIModal ?
        <div className="api-modal">
            <div>
              <p>Select Environment:</p>
              <div>
                <input type="radio" id="development" name="API_ENV" defaultValue="development" checked={API_ENV ==='development'} onChange={apiEnvChanged} />
                <label htmlFor="development">Development</label>
              </div>
              <div>
                <input type="radio" id="staging" name="API_ENV" defaultValue="staging" checked={API_ENV ==='staging'} onChange={apiEnvChanged} />
                <label htmlFor="staging">Staging</label>
              </div>
              <div>
                <input type="radio" id="production" name="API_ENV" defaultValue="production"checked={API_ENV ==='production'} onChange={apiEnvChanged}/>
                <label htmlFor="production">Production</label>
              </div>
            </div>
        </div> : ''
      }
		</div>
	);
}
